@import "vars";
@import "fonts";
@import "libs";

.container, .container-fluid {
  padding: 0 2rem; }

.row {
  margin-left: -2em;
  margin-right: -2em; }

::placeholder {
	color: #666; }

::selection {
	background-color: $accent;
	color: #fff; }

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1;
  letter-spacing: .1em;
  font-family: $default-font;
  color: $dark-gray;
  overflow-x: hidden;
  input, textarea {
    border: #666 1px solid;
    outline: none;
    &:focus:required:invalid {
      border-color: red; }
    &:required:valid {
      border-color: green; } } }

section {
  overflow: auto; }

h1, h2 {
  font-weight: 600;
  font-size: 4em;
  text-align: center; }

h3 {
  font-weight: 500;
  font-size: 3em; }

h1, h2, h3 {
  text-transform: uppercase;
 }  // margin-bottom: 1.5rem

p {
  padding: 0;
  margin: 0 0 1rem 0;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.6;
  letter-spacing: .1em;
  &.subtitle {
    font-size: 1.5rem;
    text-align: center; }
  &.description {
    font-size: 1.5rem;
    font-weight: 200; } }

.details {
  font-size: .75em;
  font-weight: 20;
  &__link {
    font-weight: 500; } }

.phone {
    font-size: 1em;
    font-weight: 600;
    text-align: right;
    padding: .25em 0 calc(.25em - 2px) 0;
    border-bottom: 2px solid rgba($dark-gray, 0);
    transition: border-color ease-in .25s;
    &:hover {
      border-color: rgba($dark-gray, 1); } }
a {
  color: $dark-gray;
  transition: color ease-in .25s;
  &:hover {
    text-decoration: none;
    color: darken($dark-gray, 25%); } }

.img-responsive {
	display: block;
	max-width: 100%;
	height: auto;
	&.img-width {
		width: 100%; } }

.text-center {
	text-align: center; }

b, strong {
	font-weight: 600; }

.header {
  padding-top: 1em;
  padding-bottom: 1em;
  position: absolute;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100vw; }

.logo {
  height: auto;
  width: 100%;
 }  // background: url(logo.svg)

nav {
  overflow: visible; }

.menu {
  text-align: right;
  display: flex;
  align-items: center;
  justify-content: flex-end; }

.link {
  color: rgba($dark-gray, .5);
  transition: color ease-in .25s;
  &__active {
    color: rgba($dark-gray, 1); }
  &:hover {
    color: $dark-gray; }
  &:focus {
    text-decoration: none; } }

.menu__item {
  margin: 0;
  margin-left: 1em; }

.btn {
  cursor: pointer;
  transition: box-shadow ease-in .25s;
  width: 100%;
  margin-right: 1rem;
  &:hover {
    box-shadow: 2px 2px 10px rgba($dark-gray, .3); }
  &_primary {
    color: $white;
    text-transform: uppercase;
    background-color: $accent;
    border: none;
    &:hover {
      color: $white; } }
  &_secondary {
    color: $white;
    text-transform: uppercase;
    background-color: rgba($light-gray, .5);
    border: none;
    &:hover {
      color: $white; } }
  &_medium {
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: .1em;
    padding: 1em 1.5em;
    border-radius: .5rem; } }


.cta-screen {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-size: cover; }

.form {
  width: 100%;
  margin-top: 3rem;
  align-items: flex-end; }

.input {
  padding: 1rem 1rem 1rem 1.5rem;
  // color: $light-gray
  background-color: $white;
  border-radius: .5em;
  border: none;
  line-height: 1; }

.form__input {
  width: 100%; }

.form__element {
  margin-bottom: 1rem !important; }

.form__details {
  margin-top: 1.5em;
  text-align: center; }



.ant-alert-message {
    margin-top: .25rem; }

.ant-modal-confirm-body .ant-modal-confirm-title {
    font-size: 1.25rem; }
.ant-modal-body {
    padding: 3rem !important; }

.ant-checkbox-checked .ant-checkbox-inner {
  background-color: $accent !important;
  border-color: $accent !important; }

.ant-btn-primary {
  background-color: $accent !important;
  border-color: $accent !important; }

